import React from 'react';
import { default as MuiCardActionArea, CardActionAreaProps } from '@mui/material/CardActionArea';

const CardActionArea = React.forwardRef<HTMLElement, CardActionAreaProps>( ( props, ref ) => {
	return (
		<MuiCardActionArea
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<CardActionAreaProps>;

export default CardActionArea;
